<template>
  <div>
    <section class="pi-content">
      <div class="container">
        <div class="pi-content__body"
          v-if="selectType"
        >
          <div class="pi-content__img">
            <img :src="selectType.img" :alt="data.name">
            <p>{{ data.name }}</p>
          </div>
          <div class="pi-content__list">
            <div class="pi-content__blocks">
              <div 
                class="pi-content__block"
                v-for="(type, idx) in data.list"
                :key="idx"
                :class="[(idx === activeType) ? 'active' : '']"
                @click="selectedType(type, idx)"
              >
                <img :src="type.img" alt="">
                <p>{{ type.name }}</p>
              </div>
            </div>
            <div class="pi-content__btn">
              <button class="btn-1" @click="openModalRecord">Заказать</button>
            </div>
          </div>
        </div>
      </div>
    </section>

    <app-record 
      v-if="modalRecord"
      @emitclosemodal="modalRecord = false"
    >
    </app-record>
      
  </div>
</template>

<script>

export default {
  name: 'ProductonItem',
  components: {
    'app-record': () => import('../components/Record.vue'),
  },
  props: ['data'],
  data() {
    return {
      selectType: null,
      activeType: null,

      modalRecord: false,
    };
  },
  created() {
    if (this.data.list) {
      this.selectType = this.data.list[0];
      this.activeType = 0;
    }
  },
  methods: {
    selectedType(type, idx) {
      this.selectType = type;
      this.activeType = idx;
    }
  }
}
</script>

